.header {
  height: 230px;
  padding-top: 50px;
  background-color: $primary-text-color;

  margin-left: auto;
  margin-right: auto;

  padding-left: 20px;
  padding-right: 20px;

  max-width: 320px;

  @media screen and (min-width: 320px) {
    max-width: 767px;
  }

  @media screen and (min-width: 768px) {
    max-width: 1023px;
    padding-left: 75px;
    padding-right: 75px;
  }
  @media screen and (min-width: 1024px) {
    max-width: 1024px;
    padding-left: 71px;
    padding-right: 71px;
  }

  background-image: url('../images/Rectangle320_1x.jpg');
  background-position: center;
  background-repeat: no-repeat;
  
  @media screen and (min-width: 320px) {
    background-image: url('../images/Rectangle768_1x.jpg');
    
  }

  @media screen and (min-width: 768px) {
    background-image: url('../images/Rectangle1024_1x.jpg');
    
}
@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  background-image:  url('../images/Rectangle1024_2x.jpg');
  }
  
}
.header .library-filter {
  display: none;
}

.header_box {
  display: flex;
  justify-content: space-between;
  align-content: flex-end;
  margin-bottom: 50px;
  
}

.header_logo_box {
  display: flex;
}
.header_logo_svg {
  margin-right: 10px;
}
.title {
  position: absolute;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  overflow: hidden;
  user-select: none;
  font-size: 30px;
  font-weight: 500;
  line-height: 0.9;
  color: $secondary-text-color;

  @media screen and (min-width: 480px) {
    position: relative;
    width: auto;
    height: auto;
    clip: auto;
    user-select: auto;
  }
}
.navigation {
  display: flex;
}
.navigation_home {
  margin-right: 40px;
}
.navigation__link {
  position: relative;
  font-size: 12px;
  line-height: 0.9;
  text-transform: uppercase;
  color: $secondary-text-color;
  transition-property: color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

}
.navigation__link:hover,
.navigation__link:focus {
  color: $accent-text-color;
}
.navigation__link.current::after {
  position: absolute;
  bottom: -3px;
  @media screen and (min-width: 768px) {
  }
  content: " ";
  display: flex;
  width: 100%;
  height: 3px;
  background-color:$accent-text-color;
}
.navigation__link:first-of-type {
  margin-right: 40px;
}
.current::after {
  color: $accent-text-color;
  content: '';
  display: block;
  position: absolute;
  bottom: -4px;
  left: 0;
  background-color: $accent-text-color;
  width: 100%;
  height: 3px;
}
.header .library-filter {
  display: none;
}


// ... my library ...

.header.page-my-library {
  background-image: url('../images/my-library320_1x.jpg');

  @media screen and (min-width: 320px) {
    background-image: url('../images/my-library768_1x.jpg');
  }

  @media screen and (min-width: 768px) {
    background-image: url('../images/my-library1024_1x.jpg');
  }
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image:  url('../images/my-library1024_2x.jpg');
    }
    .search {
      display: none;
    }
}
.librarry-filter {
  display: flex;
  justify-content: space-between;

  @media screen and (min-width: 320px) {
    justify-content: center;
  }

  & .link {
    width: 130px;
    height: 44px;

    border-radius: 5px;
    border: 1px solid #ffffff;

    font-size: 12px;
    font-weight: 500;
    line-height: 44px;
    text-align: center;
    text-transform: uppercase;
    color: $secondary-text-color;
    background-color: transparent;

    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
      border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    @media screen and (min-width: 480px) {
      width: 136px;
    }
  }
    & .link:hover,
    & .link:focus {
      border-color: $accent-text-color;
      transform: scale(1.1);
    }

    & .watched-link {
      @media screen and(min-width: 320px) {
        margin-right: 15px;
      }
    }

    & .button.is-active {
      border-color: transparent;
      background-color: $accent-text-color;

      box-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);
    }
  }


.header.page-my-library .library-filter {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

@media screen and (min-width: 320px) {
  .header.page-my-library .library-filter {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
}
.search__input {
  //   position: relative;
  font-size: 14px;
  color: $secondary-text-color;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid ;

  width: 320px;

  @media screen and (min-width: 768px) {
    width: 340px;
  }
}
::placeholder {
  font-size: 14px;
  color: $secondary-text-color;
}
.search {
  //   position: relative;
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}
.search__button {
  //   position: absolute;
  background-color: transparent;
  border: none;
  right: 0;
}
