.header-box {
  margin-bottom: 45px;
}


// ... page details ...

.header.page-details {
background-image: url('../images/page-details320_1x.jpg');
background-position: 75%;

@media screen and (min-width: 320px) {
  background-image: url('../images/page-details1024_1x.jpg');
}

.home-search {
  display: none;
}

.librarry-filter {
  display: none;
}
}

// Главные кнопки библиотеки

.library-button {
  display: inline-block;
  padding: 10px 13px;
  height: 44px;
  width: 136px;
  border-radius: 5px;
  border: 1px solid $secondary-text-color;
  color: $secondary-text-color;
  background-color: transparent;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  @media screen and (min-width: 768px) {
    width: 129px;
  }
padding: 15px 38px 13px 38px;
color: $secondary-text-color;
transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);;

&:hover,
&:focus {
  color: $accent-text-color;
  border: 1px solid $accent-text-color;
}
}

.watched-link {
margin-right: 15px;
}

.library-button-is-active {
background-color: $accent-text-color;
border: none;
box-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);

&:hover,
&:focus {
  color: $primary-text-color;
}
}
